@import "../../../style-config/mixins.scss",
  "../../../style-config/variable.scss";

.project-show-item {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-left: -10px;
  margin-right: -10px;
  // @include small-screen{
  //   height: 570px;
  // }
  .project-item {
    width: 140px;
    height: 150px;
    overflow: hidden;
    margin: 10px;
    position: relative;
    cursor: pointer;
    @include transition;
    @include small-screen{
      width: 265px;
      height: 265px;
      overflow: hidden;
      border-radius: 16px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      display: block;
    }
    .meta-content {
      visibility: visible;
      opacity: 1;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 10px;
      width: 100%;
      border-radius: 10px;
      @include glassmorphism-effect;
      @include transition;
      @include small-screen{
        width: fit-content;
        visibility: hidden;
        margin-right: 20px;
        border-radius: 10px;
        left: 20px;
        padding: 10px;
      }
      h3 {
        color: $black;
        font-weight: 400;
        margin-bottom: 10px;
      }
      .go-to-cta {
        display: flex;
        align-items: center;
        .tex {
          @include typography(10px, 400, 10px);
        }
        .arrow-icon {
          width: 15px;
          height: 15px;
          margin-left: 5px;
        }
      }
    }
    &.zoomIn{
        transform: scale(0);
        animation: zoomIn 400ms cubic-bezier(0.175, 0.885, 0.32, 1.275) forward;
    }
    &.zoomOut{
        transform: scale(1);
        animation: zoomOut 400ms cubic-bezier(0.175, 0.885, 0.32, 1.275) forward;
    }
    &:hover {
     @include small-screen{
      z-index: 1;
      transform: scale(1.2);
      box-shadow: 0 0 33px -8px rgba(0, 0, 0, 0.75);
      .meta-content {
        visibility: visible;
        opacity: 1;
        transform: translateY(-20px);
      }
     }
    }
  }
}
@keyframes zoomIn{
    0%{
        transform: scale(0);
    }
    100%{
        transform: scale(1);
    }
}
@keyframes zoomOut{
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(0);
    }
}
@import "../../../style-config/mixins.scss",
  "../../../style-config/variable.scss";
.loadMore{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}
.call-to-action {
  height: 47px;
  width: fit-content;
  padding: 0 15px;
  background-color: $white;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $black-light;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  @include typography(18px, 400, 1);

  .text,
  .arrow-icon,
  .icon {
    position: relative;
    z-index: 1;
    transition: all ease 500ms;
  }

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: $yellow;
    z-index: 0;
    transform: translateX(-100%);
    transition: all 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }

  &:hover {
    &:before {
      transform: translateX(0);
    }

    .arrow-icon,
    .icon {
      background-color: $white;
    }
  }

  .icon {
    width: 21px;
    height: 21px;
    background-color: $yellow;
    border-radius: 41px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    font-size: 12px;
  }
}

